.carousel {
  background-size: cover;
  position: relative;
}
.carousel-item::before {
  content: "";
  background: rgba(13, 20, 26, 0.1);
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
}
.carousel-caption {
  bottom: 30%;
}
.carousel-caption h1 {
  color: white;
  font-size: 50px;
  font-weight: 700;
  margin: 0 0 10px 0;
  text-transform: uppercase;
  line-height: 56px;
}
.carousel-caption h2 {
  color: white;
  margin-bottom: 50px;
  font-size: 25px;
  line-height: 1.5;
  text-transform: uppercase;
  text-align: center;
}
.carousel-caption h2:hover {
  color: var(--first-color);
}
.carousel-caption .getStart {
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 14px;
  display: inline-block;
  padding: 10px 35px 10px 35px;
  border-radius: 10px;
  transition: 0.5s;
  color: #fff;
  border: 1px solid #85807f;

  background: var(--first-color);
  text-decoration: none;
}
.carousel-caption .getStart:hover {
  border-color: var(--first-color);
  background: rgba(255, 255, 255, 0.1);
}

@media (max-width: 768px) {
  .carousel-caption {
    padding: 5px;
  }
  .carousel-caption h1 {
    font-size: 20px;
    line-height: 20px;
  }
  .carousel-caption h2 {
    font-size: 12px;
    line-height: 10px;
    margin: 10px;
  }
  .carousel-caption .getStart {
    font-weight: 500;
    font-size: 14px;
    display: inline-block;
    padding: 5px 25px 5px 25px;
  }
}

/*video Landing*/
.player-wrapper {
  position: relative;
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
}

.carousel {
  width: 100%;
  height: 100%;
  margin: auto;
}

.carousel .carousel-control-prev-icon {
  visibility: hidden;
}
.carousel:hover .carousel-control-prev-icon {
  visibility: visible;
}

.carousel .carousel-control-next-icon {
  visibility: hidden;
}
.carousel:hover .carousel-control-next-icon {
  visibility: visible;
}
